	.reasons {
		padding: 38px 0 36px 0;
		&_block {
			margin-top: 26px; }
		&_round {
			width: 100px;
			height: 100px; }
		&_descr {
			width: 100%;
			margin-top: 27px; }
		&_subtitle {
			font-size: 16px; } }

	.title {
		font-size: 16px;
		margin-top: 15px;
