	.es_hidden {
		display: none; }
	.es_visible {
		display: block; }
	.label_white {
		margin-bottom: 249px;
		line-height: 6px; }

	.world {
		padding: 25px 0 31px 0;
		min-height: 360px;
		height: 360px;
		.label_white {
			margin-bottom: 14px; }
		.title_white {
			margin-top: 0;
			font-size: 16px;
			line-height: 16px; }
		.subtitle_white {
			font-size: 14px;
			line-height: 21px;
			margin-top: 7px; }
		.linkcity {
			margin-top: 205px; } }

	.footer {
		min-height: 253px;
		margin: 0;
		&_main {
			&_str {
				margin-top: 17px;
				max-height: 100%;
				a {
					font-size: 13px; } } }

		&_second {
			&_str {

				max-height: 100%;
				margin-top: 6px;
				a {
					font-size: 11px; } }
			&_str.spc {
				margin-top: 4px; } }
		&_third {
			&_str {
				a {
					font-size: 11px; } } }
		.icon_h_s {
			display: inline-block;
			margin: 0 auto;
			a {
				margin-right: 9px; } }
		.icon_h_d {
			margin-top: 0;
			margin-bottom: 0; }
		.icons1 {
			display: flex;
			justify-content: center; }
		.xs-visible.row.align-items-start {
